<template>
  <div id="social-media-card-component" :class="socialMediaClass">
    <div class="avatar-container">
      <div class="avatar">
        <font-awesome-icon
          class="icon"
          :icon="['fas', 'user']"
        ></font-awesome-icon>
      </div>
    </div>
    <div class="content-container">
      <h3 class="name">Brand Name</h3>
      <div class="info-container">
        <div class="item-container">
          <p class="count">771</p>
          <p class="label">posts</p>
        </div>
        <div class="item-container">
          <p class="count">102.4k</p>
          <p class="label">followers</p>
        </div>
        <div class="item-container">
          <p class="count">828</p>
          <p class="label">following</p>
        </div>
      </div>
      <div class="hashtag-container">
        <p>#profit</p>
      </div>
    </div>
    <div v-if="type === SOCIAL_MEDIA.INSTAGRAM" class="media-icon-container">
      <font-awesome-icon
        class="icon"
        :icon="['fab', 'instagram']"
      ></font-awesome-icon>
    </div>
  </div>
</template>

<script lang="ts">
import { defineComponent, toRefs, computed } from "vue";
import { SOCIAL_MEDIA } from "@/constants/kyc";

const Component = defineComponent({
  name: "SocialMediaCard",
  props: {
    type: {
      type: Number,
      default: -1
    }
  },
  setup(props) {
    const { type } = toRefs(props);

    const socialMediaClass = computed(() => {
      if (type.value < 0) return "";
      return SOCIAL_MEDIA[type.value];
    });

    return {
      socialMediaClass,
      SOCIAL_MEDIA
    };
  }
});

export default Component;
</script>

